:root {
  --main-font-hover-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
}

.list__items {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover, .nav_link.is-active {
  color: var(--main-font-hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-font-hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

